import React, { useState } from 'react';
import { Mail, Github, Linkedin, Sun, Moon } from 'lucide-react';
import { ThemeProvider, useTheme } from '../contexts/ThemeContext';
import Education from './Education';
import Certifications from './Certifications';
import Projects from './Projects';
import Experience from './Experience';
import Activities from './Activities';
import Hero from './Hero';
import About from './About';
import Skills from './Skills';
import Contact from './Contact';

const NAV_SECTIONS = [
  { id: 'about', label: 'About' },
  { id: 'education', label: 'Education' },
  { id: 'certifications', label: 'Awards' },
  { id: 'projects', label: 'Projects' },
  { id: 'experience', label: 'Experience' },
  { id: 'activities', label: 'Activities' },
  { id: 'skills', label: 'Skills' },
  { id: 'contact', label: 'Contact' },
];

const PortfolioContent = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const { isDark, toggleTheme } = useTheme();

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
      setIsMenuOpen(false);
    }
  };

  return (
    <div className={`min-h-screen transition-colors duration-300 ${isDark ? 'bg-slate-950 text-slate-100' : 'bg-brand-surface text-brand-heading'}`}>
      <div className="relative min-h-screen">
        <div className="absolute inset-0 -z-10">
          <div className="h-72 bg-gradient-to-b from-brand-secondary/30 via-transparent to-transparent" />
        </div>

        <nav className={`fixed top-0 left-0 right-0 z-50 backdrop-blur-lg border-b transition-colors duration-300 ${
          isDark ? 'bg-slate-950/80 border-white/10' : 'bg-white/80 border-white/40'
        }`}>
          <div className="max-w-6xl mx-auto px-6">
            <div className="flex justify-between items-center py-4">
              <div>
                <p className="text-xs uppercase tracking-[0.5em] text-brand-secondary">Portfolio</p>
                <p className="text-2xl font-bold"></p>
              </div>

              <div className="hidden md:flex items-center space-x-6">
                {NAV_SECTIONS.map(({ id, label }) => (
                  <button
                    key={id}
                    type="button"
                    onClick={() => scrollToSection(id)}
                    className={`text-sm font-semibold tracking-tight transition-colors ${
                      isDark ? 'text-slate-200 hover:text-brand-secondary' : 'text-brand-muted hover:text-brand-primary'
                    }`}
                  >
                    {label}
                  </button>
                ))}
                <button
                  onClick={toggleTheme}
                  aria-label="Toggle theme"
                  className={`p-2 rounded-xl border transition-colors ${
                    isDark ? 'border-white/10 text-yellow-300 hover:bg-white/5' : 'border-brand-secondary/40 text-brand-primary hover:bg-brand-secondary/10'
                  }`}
                >
                  {isDark ? <Sun size={18} /> : <Moon size={18} />}
                </button>
              </div>

              <div className="md:hidden flex items-center space-x-2">
                <button
                  onClick={toggleTheme}
                  aria-label="Toggle theme"
                  className={`p-2 rounded-xl border transition-colors ${
                    isDark ? 'border-white/10 text-yellow-300 hover:bg-white/5' : 'border-brand-secondary/40 text-brand-primary hover:bg-brand-secondary/10'
                  }`}
                >
                  {isDark ? <Sun size={18} /> : <Moon size={18} />}
                </button>
                <button
                  onClick={() => setIsMenuOpen(!isMenuOpen)}
                  aria-label="Toggle navigation menu"
                  className={`p-2 rounded-xl border transition-colors ${
                    isDark ? 'border-white/10 text-slate-200 hover:bg-white/5' : 'border-brand-secondary/40 text-brand-primary hover:bg-brand-secondary/10'
                  }`}
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    {isMenuOpen ? (
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    ) : (
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                    )}
                  </svg>
                </button>
              </div>
            </div>
          </div>

          {isMenuOpen && (
            <div className={`md:hidden border-t transition-colors duration-300 ${isDark ? 'border-white/10 bg-slate-950/90' : 'border-white/60 bg-white/90'}`}>
              <div className="px-6 py-4 space-y-3">
                {NAV_SECTIONS.map(({ id, label }) => (
                  <button
                    key={id}
                    type="button"
                    onClick={() => scrollToSection(id)}
                    className={`w-full text-left text-sm font-medium py-2 ${
                      isDark ? 'text-slate-200 hover:text-brand-secondary' : 'text-brand-muted hover:text-brand-primary'
                    }`}
                  >
                    {label}
                  </button>
                ))}
              </div>
            </div>
          )}
        </nav>

        <Hero />
        <main className="relative space-y-24 pb-16">
          <About />
          <Education />
          <Certifications />
          <Projects />
          <Experience />
          <Activities />
          <Skills />
          <Contact />
        </main>

        <footer className={`py-10 text-center border-t transition-colors duration-300 ${
          isDark ? 'bg-slate-950 border-white/5' : 'bg-white border-white/60'
        }`}>
          <div className="max-w-6xl mx-auto px-6">
            <div className="flex justify-center items-center space-x-6 mb-4">
              {[{ icon: Mail, href: 'mailto:koo0685@naver.com' }, { icon: Github, href: 'https://github.com/kookyungseon' }, { icon: Linkedin, href: '#' }].map(({ icon: Icon, href }) => (
                <a
                  key={href}
                  href={href}
                  className={`p-3 rounded-full border transition-colors ${
                    isDark ? 'border-white/10 text-slate-200 hover:text-brand-secondary' : 'border-brand-secondary/40 text-brand-muted hover:text-brand-primary'
                  }`}
                  target={href.startsWith('http') ? '_blank' : undefined}
                  rel={href.startsWith('http') ? 'noopener noreferrer' : undefined}
                >
                  <Icon size={20} />
                </a>
              ))}
            </div>
            <p className="text-sm text-brand-muted dark:text-slate-400"> 2024 . All rights reserved.</p>
            <p className="text-xs text-brand-muted/80 dark:text-slate-500 mt-2">Built with React  Tailwind CSS</p>
          </div>
        </footer>
      </div>
    </div>
  );
};

const PortfolioMain = () => {
  return (
    <ThemeProvider>
      <PortfolioContent />
    </ThemeProvider>
  );
};

export default PortfolioMain;