import React from 'react';

const SectionHeader = ({ eyebrow, title, description, align = 'center' }) => {
  const alignmentClass = align === 'left' ? 'text-left items-start' : 'text-center items-center';

  return (
    <div className={`flex flex-col gap-3 mb-12 ${alignmentClass}`}>
      {eyebrow && (
        <span className="text-xs font-semibold tracking-[0.3em] uppercase text-brand-secondary">
          {eyebrow}
        </span>
      )}
      <h2 className="text-4xl font-bold text-brand-heading dark:text-white leading-tight">
        {title}
      </h2>
      {description && (
        <p className="text-lg text-brand-muted dark:text-slate-300 max-w-3xl">
          {description}
        </p>
      )}
      <div className="w-16 h-1 rounded-full bg-gradient-to-r from-brand-secondary to-brand-primary" />
    </div>
  );
};

export default SectionHeader;

