import React, { useState, useEffect, useRef } from "react";
import { Calendar, CheckCircle, Code2, Cloud, Building, MapPin, ChevronDown, ChevronUp } from "lucide-react";
import SectionHeader from "./SectionHeader";
import { useTheme } from "../contexts/ThemeContext";

const getTimeValue = (value) => (value ? new Date(value).getTime() : 0);

const Experience = () => {
  const [visibleCards, setVisibleCards] = useState({});
  const [expandedItems, setExpandedItems] = useState({});
  const cardRefs = useRef([]);
  const { isDark } = useTheme();

  const sectionBg = isDark ? 'bg-slate-950' : 'bg-white';
  const cardBg = isDark ? 'bg-slate-900/60 text-slate-100 border border-white/5' : 'bg-white text-brand-muted border border-slate-100';
  const timelineLine = isDark ? 'bg-brand-secondary/40' : 'bg-brand-secondary/60';
  const timelineDot = isDark ? 'bg-slate-800 text-brand-secondary' : 'bg-brand-secondary text-white';

  const experiences = [
    {
      company: "",
      position: "   (MSA)  ",
      period: "2022.08.22 ~ 2023.02.16",
      location: " ",
      startDate: "2022-08-22",
      endDate: "2023-02-16",
      description: "944       ,   ,      Docker Kubernetes         . AI      ''    AI           .",
      achievements: [
        " 944     ",
        "    (  8h,    128h,   264h)",
        "Hybrid (104h) MSA           ",
        "Kubernetes    Docker           ",
        "AI      ''   : AWS                 ",
        "Route 53  , , RDS  , Lambda   , Docker     Kubernetes            ",
        "            ",
        "       (MSA)    "
      ],
      technologies: ["AWS", "MSA", "Docker", "Kubernetes", "DevOps", "Python", "Java", "Route 53", "RDS", "Lambda", "AI/ML"],
      icon: Cloud
    },
    {
      company: "()",
      position: "",
      period: "2024.07 ~ 2024.08",
      location: "",
      startDate: "2024-07-01",
      endDate: "2024-08-31",
      description: "                   ",
      achievements: [
        "(PHP CMS)  FTP   ,  ,        ",
        "  API (Papago/Google Translate )            ",
        "        ,  ,       ",
        "             ,       ",
        "       UI    ",
        "  +            ",
        ",         ",
        "         ",
        "       ,        "
      ],
      technologies: ["PHP", "GnuBoard", "Translation API", "FTP", "MySQL", "JavaScript", "Papago API", "Google Translate API", "Caching"],
      icon: Building
    },
    {
      company: "   ",
      position: "   - Flutter   &  ",
      period: "2025.05.05 ~ 2025.06.27",
      location: " ",
      startDate: "2025-05-05",
      endDate: "2025-06-27",
      description: "       ,        AI   ' '         ",
      achievements: [
        "Flutter    ,        UIUX    ",
        "Spring BootFastAPI  REST API   , Whisper , GPT , GPS       ",
        "      UX       ",
        "Google Maps API   UI         ",
        "       ,  ,              ",
        "     + AI  +        (MVP) ",
        "    ' '  UX    ",
        "    , 2025    (  )  () "
      ],
      technologies: ["Flutter", "Dart", "Spring Boot", "FastAPI", "REST API", "Whisper", "GPT", "Google Maps API", "UI/UX Design", "Team Management"],
      icon: Code2
    },
    {
      company: " ",
      position: " ",
      period: "2024.09.02 ~ 2025.11.13",
      location: "",
      startDate: "2024-09-02",
      endDate: "2025-11-13",
      description: " LLM       AI   ",
      achievements: [
        "      ",
        "LangChain, LangGraph   () ",
        "PDF      ",
        "OCR  CLIP       ",
        "          "
      ],
      technologies: ["LangChain", "LangGraph", "Python", "OCR", "CLIP", "AI/ML", "Multimodal"],
      icon: Code2
    }
  ];

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setVisibleCards((prev) => ({
              ...prev,
              [entry.target.dataset.index]: true,
            }));
          }
        });
      },
      { threshold: 0.1 }
    );

    cardRefs.current.forEach((cardRef) => {
      if (cardRef) {
        observer.observe(cardRef);
      }
    });

    return () => observer.disconnect();
  }, []);

  const sortedExperiences = [...experiences].sort((a, b) => {
    const endDiff = getTimeValue(b.endDate) - getTimeValue(a.endDate);
    if (endDiff !== 0) return endDiff;
    return getTimeValue(b.startDate) - getTimeValue(a.startDate);
  });

  return (
    <section id="experience" className={`py-20 px-6 transition-colors duration-300 ${sectionBg}`}>
      <div className="max-w-6xl mx-auto">
        <SectionHeader
          eyebrow="Experience"
          title="   &  "
          description=",  ,        ."
        />
        {/*    */}
        <div className="relative">
          {/*   */}
          <div className={`absolute left-8 top-0 bottom-0 w-1 ${timelineLine} rounded-full`}></div>
          
          <div className="space-y-12">
            {sortedExperiences.map((exp, index) => (
              <div
                key={index}
                ref={(el) => (cardRefs.current[index] = el)}
                data-index={index}
                className={`relative flex items-start transform transition-all duration-700 ease-out ${
                  visibleCards[index] ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'
                }`}
                style={{ transitionDelay: `${index * 100}ms` }}
              >
                {/*   */}
                <div className="relative z-10 flex-shrink-0 mr-8">
                  <div className={`w-16 h-16 rounded-full border-4 border-white/70 shadow-lg flex items-center justify-center ${timelineDot}`}>
                    <exp.icon size={24} />
                  </div>
                </div>
                
                {/*   */}
                <div className="flex-1">
                  <div className={`rounded-2xl p-8 shadow-soft hover:shadow-card transition-all duration-300 ${cardBg}`}>
                    <div className="flex items-start justify-between mb-6">
                      <div>
                        <h3 className="text-2xl font-bold text-brand-heading dark:text-white">{exp.company}</h3>
                        <p className="text-lg text-brand-secondary font-medium">{exp.position}</p>
                      </div>
                      <div className="text-right">
                        <div className="flex items-center space-x-2 text-brand-secondary mb-2">
                          <Calendar size={16} />
                          <span className="text-sm">{exp.period}</span>
                        </div>
                        <div className="flex items-center space-x-2 text-brand-secondary">
                          <MapPin size={16} />
                          <span className="text-sm">{exp.location}</span>
                        </div>
                      </div>
                    </div>

                    <p className="text-brand-muted dark:text-slate-200 mb-6 text-lg">
                      {exp.description}
                    </p>

                    <div className="mb-6">
                      <h4 className="text-lg font-semibold text-brand-heading dark:text-white mb-3">
                          ({exp.achievements.length})
                      </h4>
                      <ul className="space-y-2">
                        {exp.achievements.slice(0, expandedItems[index] ? exp.achievements.length : 3).map((achievement, i) => (
                          <li key={i} className="flex items-start text-brand-muted dark:text-slate-200">
                            <CheckCircle size={18} className="text-brand-secondary mr-2 flex-shrink-0 mt-1" />
                            <span>{achievement}</span>
                          </li>
                        ))}
                      </ul>
                      {exp.achievements.length > 3 && (
                        <button
                          onClick={() => setExpandedItems(prev => ({ ...prev, [index]: !prev[index] }))}
                          className="mt-3 flex items-center text-brand-secondary hover:text-brand-primary transition-colors text-sm font-medium group"
                        >
                          {expandedItems[index] ? (
                            <>
                              <span> </span>
                              <ChevronUp size={14} className="ml-1 group-hover:translate-y-[-2px] transition-transform" />
                            </>
                          ) : (
                            <>
                              <span>  ({exp.achievements.length - 3} )</span>
                              <ChevronDown size={14} className="ml-1 group-hover:translate-y-[2px] transition-transform" />
                            </>
                          )}
                        </button>
                      )}
                    </div>

                    <div>
                      <h4 className="text-lg font-semibold text-brand-heading dark:text-white mb-3"> </h4>
                      <div className="flex flex-wrap gap-2">
                        {exp.technologies.map((tech, i) => (
                          <span
                            key={i}
                            className="px-3 py-1 bg-brand-secondary/15 text-brand-primary dark:text-white rounded-full text-sm font-medium"
                          >
                            {tech}
                          </span>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Experience;