import React, { useState, useEffect, useRef } from "react";
import { Calendar, Heart, MapPin, Leaf, Zap } from "lucide-react";

const getTimeValue = (value) => (value ? new Date(value).getTime() : 0);

const Activities = () => {
  const [visibleCards, setVisibleCards] = useState({});
  const cardRefs = useRef([]);

  const activities = [
    {
      title: "   ",
      type: " ",
      period: "2021",
      location: "",
      startDate: "2021-01-01",
      endDate: "2021-12-31",
      description: "2050                  ",
      teamInfo: ": NOC | : ",
      achievements: [
        "     :          ,     ",
        "SNS     :   @n_o__c     , ,      ",
        "   :  ,            ",
        "       : ,              ",
        "    :              ",
        "    :           "
      ],
      skills: ["Environmental Policy", "Content Creation", "SNS Marketing", "Campaign Planning", "Team Collaboration"],
      icon: Leaf
    },
    {
      title: "  ",
      type: " ",
      period: "2020.06.25 ~ 2020.12.31",
      location: "",
      startDate: "2020-06-25",
      endDate: "2020-12-31",
      description: "             ",
      achievements: [
        "      :            ",
        "   :             ",
        "    :             ",
        "  :     ",
        "  :         ",
        "   :   ,           "
      ],
      skills: ["Public Policy", "Social Campaign", "Community Engagement", "Energy Welfare"],
      icon: Zap
    },
    {
      title: " 21  ",
      type: "",
      period: "2020.08.10 ~ 2020.08.14",
      location: "",
      startDate: "2020-08-10",
      endDate: "2020-08-14",
      description: "        ",
      achievements: [
        " ,            ",
        "          ",
        "DNA         ",
        "       "
      ],
      skills: ["Science Education", "Teaching", "Volunteering", "Communication"],
      icon: Heart
    }
  ];

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setVisibleCards((prev) => ({
              ...prev,
              [entry.target.dataset.index]: true,
            }));
          }
        });
      },
      { threshold: 0.1 }
    );

    cardRefs.current.forEach((cardRef) => {
      if (cardRef) {
        observer.observe(cardRef);
      }
    });

    return () => observer.disconnect();
  }, []);

  const sortedActivities = [...activities].sort((a, b) => {
    const endDiff = getTimeValue(b.endDate) - getTimeValue(a.endDate);
    if (endDiff !== 0) return endDiff;
    return getTimeValue(b.startDate) - getTimeValue(a.startDate);
  });

  return (
    <section id="activities" className="py-20 px-6 bg-white">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-16">
          <h2 className="text-4xl font-bold text-[#2D3748] mb-4"> Activities</h2>
          <p className="text-lg text-[#4A5568] max-w-2xl mx-auto">
                    
          </p>
        </div>

        {/*   */}
        <div className="relative">
          {/*   */}
          <div className="absolute left-8 top-0 bottom-0 w-1 bg-gradient-to-b from-[#9ECAD6] to-[#748DAE] rounded-full"></div>
          
          <div className="space-y-12">
            {sortedActivities.map((activity, index) => (
              <div
                key={index}
                ref={(el) => (cardRefs.current[index] = el)}
                data-index={index}
                className={`relative flex items-start transform transition-all duration-700 ease-out ${
                  visibleCards[index] ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'
                }`}
                style={{ transitionDelay: `${index * 100}ms` }}
              >
                {/*   */}
                <div className="relative z-10 flex-shrink-0 mr-8">
                  <div className="w-16 h-16 bg-[#9ECAD6] rounded-full border-4 border-white shadow-lg flex items-center justify-center">
                    <activity.icon className="text-white" size={24} />
                  </div>
                </div>
                
                {/*   */}
                <div className="flex-1">
                  <div className="bg-white rounded-2xl p-8 shadow-lg hover:shadow-xl transition-all duration-300 border border-gray-100">
                    <div className="flex items-start justify-between mb-6">
                      <div>
                        <h3 className="text-2xl font-bold text-[#748DAE]">{activity.title}</h3>
                        <p className="text-lg text-[#9ECAD6] font-medium">{activity.type}</p>
                        {activity.teamInfo && (
                          <p className="text-sm text-[#4A5568] mt-1">{activity.teamInfo}</p>
                        )}
                      </div>
                      <div className="text-right">
                        <div className="flex items-center space-x-2 text-[#9ECAD6] mb-2">
                          <Calendar size={16} />
                          <span className="text-sm">{activity.period}</span>
                        </div>
                        <div className="flex items-center space-x-2 text-[#9ECAD6]">
                          <MapPin size={16} />
                          <span className="text-sm">{activity.location}</span>
                        </div>
                      </div>
                    </div>

                    <p className="text-[#748DAE] mb-6 text-lg leading-relaxed">{activity.description}</p>

                    <div className="mb-6">
                      <h4 className="text-lg font-semibold text-[#748DAE] mb-3"> </h4>
                      <ul className="space-y-2">
                        {activity.achievements.map((achievement, i) => (
                          <li key={i} className="flex items-start text-[#748DAE]">
                            <span className="text-[#9ECAD6] mr-2 flex-shrink-0 mt-1"></span>
                            <span>{achievement}</span>
                          </li>
                        ))}
                      </ul>
                    </div>

                    <div>
                      <h4 className="text-lg font-semibold text-[#748DAE] mb-3"> </h4>
                      <div className="flex flex-wrap gap-2">
                        {activity.skills.map((skill, i) => (
                          <span key={i} className="px-3 py-1 bg-[#9ECAD6]/20 text-[#748DAE] rounded-full text-sm font-medium">
                            {skill}
                          </span>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Activities;
