import React, { useState } from "react";
import { Github, ArrowRight, Calendar, User, Wrench, ChevronDown, ChevronUp, Grid, List } from "lucide-react";
import SectionHeader from "./SectionHeader";

const getTimeValue = (value) => (value ? new Date(value).getTime() : 0);

const ProjectCard = ({ project, index }) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isExpanded, setIsExpanded] = useState(false);
  
  //     
  const cardStyles = [
    'bg-white dark:bg-slate-900/70',
    'bg-gradient-to-br from-brand-secondary/10 to-white dark:from-brand-secondary/5 dark:to-slate-900/60',
    'bg-gradient-to-br from-brand-accent/10 to-white dark:from-rose-400/10 dark:to-slate-900/70',
    'bg-gradient-to-br from-brand-primary/5 to-brand-secondary/5 dark:from-brand-primary/10 dark:to-slate-900/70',
    'bg-white/80 backdrop-blur-sm dark:bg-slate-900/60',
    'bg-gradient-to-br from-brand-accent/15 to-brand-secondary/10',
  ];
  
  const borderStyles = [
    'border border-slate-100 dark:border-white/5',
    'border-l-4 border-brand-secondary',
    'border-t-4 border-brand-primary',
    'border-2 border-brand-accent/60',
    'border-l-4 border-brand-accent',
    'border-t-4 border-brand-secondary/50',
  ];

  return (
    <div
      className={`group relative ${cardStyles[index % cardStyles.length]} ${borderStyles[index % borderStyles.length]} rounded-3xl shadow-soft hover:shadow-card transition-all duration-300 overflow-hidden ${
        isHovered ? 'scale-[1.01] -translate-y-1' : 'scale-100'
      }`}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/*   */}
      <div className="relative z-10 p-8">
        {/*  */}
        <div className="flex items-start justify-between mb-6">
          <div className="flex items-center space-x-4">
            <div className="p-3 bg-brand-secondary/20 dark:bg-brand-secondary/30 rounded-xl mr-4">
              <span className="text-2xl">{project.emoji}</span>
            </div>
            <div className="flex-1">
              <h3 className="text-xl font-bold text-brand-heading dark:text-white mb-1 leading-tight">{project.title}</h3>
              <div className="flex items-center space-x-2 flex-wrap">
                <span className="px-3 py-1 rounded-full text-xs font-medium bg-brand-secondary/20 text-brand-primary dark:text-white">
                  {project.category}
                </span>
                {project.award && (
                  <span className="px-3 py-1 rounded-full text-xs font-medium bg-brand-secondary/20 text-brand-primary dark:text-white border border-brand-secondary/30">
                     {project.award}
                  </span>
                )}
              </div>
            </div>
          </div>

          <div className="flex space-x-2">
            <a
              href={project.github}
              target="_blank"
              rel="noopener noreferrer"
              className="p-2 bg-brand-primary/10 hover:bg-brand-primary/20 rounded-lg transition-all duration-300 hover:scale-110 text-brand-primary"
              title="GitHub Repository"
              aria-label={`${project.title} GitHub Repository`}
            >
              <Github size={18} />
            </a>
          </div>
        </div>

        {/*  */}
        <div className="mb-4">
          <p className={`text-brand-muted dark:text-slate-200 leading-relaxed text-sm transition-all duration-300 ${
            isExpanded ? 'line-clamp-none' : 'line-clamp-3'
          }`}>
            {project.description}
          </p>
          {project.description.length > 150 && (
            <button
              onClick={() => setIsExpanded(!isExpanded)}
              className="mt-2 flex items-center text-brand-secondary hover:text-brand-primary transition-colors text-xs font-medium"
            >
              {isExpanded ? (
                <>
                  <span> </span>
                  <ChevronUp size={14} className="ml-1" />
                </>
              ) : (
                <>
                  <span> </span>
                  <ChevronDown size={14} className="ml-1" />
                </>
              )}
            </button>
          )}
        </div>

        {/*   */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4 p-3 bg-brand-secondary/10 dark:bg-white/5 rounded-xl">
          <div className="flex items-center space-x-2">
            <Calendar className="text-brand-secondary" size={14} />
            <span className="text-xs text-brand-primary dark:text-white font-medium">{project.period}</span>
          </div>
          <div className="flex items-center space-x-2">
            <User className="text-brand-secondary" size={14} />
            <span className="text-xs text-brand-primary dark:text-white font-medium">{project.role}</span>
          </div>
          <div className="flex items-center space-x-2">
            <Wrench className="text-brand-secondary" size={14} />
            <span className="text-xs text-brand-primary dark:text-white font-medium">{project.techCount} </span>
          </div>
      </div>

        {/*   */}
        <div className="mb-4">
          <h4 className="text-sm font-semibold text-brand-heading dark:text-white mb-2 flex items-center">
            <span className="mr-2"></span>
             
          </h4>
          <div className="flex flex-wrap gap-2">
            {project.technologies.map((tech, techIndex) => (
              <span
                key={techIndex}
                className="px-2 py-1 rounded-full text-xs font-medium bg-brand-secondary/15 text-brand-primary dark:text-white hover:bg-brand-secondary/25 transition-colors cursor-default"
              >
            {tech}
          </span>
        ))}
      </div>
        </div>

        {/*  */}
        <div className="flex items-center justify-between pt-3 border-t border-brand-secondary/20">
      <a
            href={project.github}
        target="_blank"
        rel="noopener noreferrer"
            className="inline-flex items-center text-brand-secondary hover:text-brand-primary transition-colors font-medium group text-sm"
          >
            <span>Repository </span>
            <ArrowRight size={14} className="ml-2 group-hover:translate-x-1 transition-transform" />
          </a>

          {project.features && (
            <div className="flex space-x-1">
              {project.features.map((feature, featureIndex) => (
                <span
                  key={featureIndex}
                  className="px-2 py-1 rounded-full text-xs font-medium bg-brand-secondary/15 text-brand-primary dark:text-white"
                >
                  {feature}
                </span>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

